.footer {
  display: grid;
  /*     grid-template-columns: repeat(5, 1fr);
 */
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 0px;
  grid-row-gap: 0px;

  a,
  a:visited,
  :active {
    color: $dark-400;
    font-weight: 500;
    font-family: $body-font-family;
    font-size: $body-font-size-small;
    text-decoration: none;
    letter-spacing: 2px;
  }
  & h4 {
    font-size: $body-font-size-medium;
    font-family: $body-font-family;
    font-weight: $font-weight-bold;
    letter-spacing: 2px;
    margin-top: 3rem;
  }

  &__header {
    text-align: center;
  }

  &__citation {
    margin: 0;
    padding: 0;

    blockquote {
      margin: 0;
      padding: 0;

      letter-spacing: 2px;
      text-align: center;
      font-family: $heading-font-family;
      font-weight: $font-weight-regular;
      font-size: $body-font-size-medium;
      p {
        margin: 0;
      }
    }
    figcaption {
      text-align: center;
    }
  }
  &__contact {
    &-position {
      p {
        letter-spacing: 2px;
        font-size: $body-font-size-small;
        font-family: $body-font-family;
        font-weight: $font-weight-regular;
        span {
          display: inline-block;
          width: 100%;
        }
      }
    }
  }
  &__menu,
  &__follow {
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
  }
  &__cgu {
    text-align: center;
  }
}
