  top: 0;
  left: calc(80% - 0.5em);
  transform: rotate(-90deg);
  color: #fcfcfc;
  mix-blend-mode: difference;
  font-weight: 700;
  font-size: 1.4rem;
  width: fit-content;
  font-family: "Raleway", sans-serif;
}
.blog__item-img {
  width: 100%;
  background: #2c2b3b;
}
.blog__item header {
  margin: 17px 12px;
  padding: 0;
}
.blog__item-header {
  margin: 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 2.9rem;
  text-align: left;
  font-weight: 400;
  font-family: "Raleway", sans-serif;
}

.footer {
  display: grid;
  /*     grid-template-columns: repeat(5, 1fr);
  */
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.footer a,
.footer a:visited,
.footer :active {
  color: #2c2b3b;
  font-weight: 500;
  font-family: "Cabin", sans-serif;
  font-size: 1.4rem;
  text-decoration: none;
  letter-spacing: 2px;
}
.footer h4 {
  font-size: 1.6rem;
  font-family: "Cabin", sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  margin-top: 3rem;
}
.footer__header {
  text-align: center;
}
.footer__citation {
  margin: 0;
  padding: 0;
}
.footer__citation blockquote {
  margin: 0;
  padding: 0;
  letter-spacing: 2px;
  text-align: center;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
}
.footer__citation blockquote p {
  margin: 0;
}
.footer__citation figcaption {
  text-align: center;
}
.footer__contact-position p {
  letter-spacing: 2px;
  font-size: 1.4rem;
  font-family: "Cabin", sans-serif;
  font-weight: 400;
}
.footer__contact-position p span {
  display: inline-block;
  width: 100%;
}
.footer__menu ul, .footer__follow ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer__cgu {
  text-align: center;
}

.blogging__content {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.blogging__searchbar {
  grid-area: 1/1/2/6;
  background: pink;
}
.blogging__text {
  grid-area: 3/1/7/5;
  background: blue;
}
.blogging__metadata {
  grid-area: 2/5/7/6;
  background: #2c2b3b;
}
.blogging__title {
  grid-area: 2/1/3/5;
  background: yellow;
}